// components/AddTenantModal.jsx
import React, { useState } from 'react';
import { Input } from './ui/input';
import { Button } from './ui/button';

const AddTenantModal = ({ onAdd, onClose }) => {
  const [name, setName] = useState('');
  const [contact, setContact] = useState('');
  const [floor, setFloor] = useState('');
  const [nid, setNid] = useState('');

  const handleAddTenant = () => {
    onAdd({ name, contact, floor, nid, bills: [] });
    onClose();
  };

  const isFormComplete = name && contact && floor && nid;

  return (
    <div className="fixed inset-0 bg-gray-800 bg-opacity-50 flex justify-center items-center">
      <div className="bg-white dark:bg-[#0C0E0F] p-6 rounded-md w-11/12">
        <h2 className="text-xl font-bold mb-4 px-4">Add Tenant</h2>
        <Input placeholder="Name" value={name} onChange={(e) => setName(e.target.value)} className="mb-4"/>
        
        <Input placeholder="Contact" value={contact} onChange={(e) => setContact(e.target.value)} className="mb-4"/>

        <Input placeholder="Floor" value={floor} onChange={(e) => setFloor(e.target.value)} className="mb-4"/>

        <Input placeholder="NID" value={nid} onChange={(e) => setNid(e.target.value)} className="mb-4"/>

        <div className="flex justify-end">
          <Button 
            onClick={handleAddTenant} 
            className="px-4 py-2 mr-2"
            disabled={!isFormComplete}
          >
            Add</Button>

          <Button onClick={onClose} className="px-4 py-2 mr-2 bg-gray-500 dark:bg-gray-900 dark:text-white">Cancel</Button>

        </div>
      </div>
    </div>
  );
};

export default AddTenantModal;