// components/AlertPopup.jsx
import React from 'react';
import { Button } from '@/components/ui/button';

const AlertPopup = ({ message, onClose }) => {
  return (
    <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
      <div className="bg-white dark:bg-[#0C0E0F] p-6 rounded-lg shadow-md max-w-sm w-11/12 text-center">
        <p className="text-md mb-4">{message}</p>
        <Button onClick={onClose} className="w-full py-2">Close</Button>
      </div>
    </div>
  );
};

export default AlertPopup;
