// contexts/TenantContext.js
import React, { createContext, useState, useEffect } from 'react';
import { collection, addDoc, getDocs, updateDoc, doc, deleteDoc } from 'firebase/firestore';
import { db } from '../firebaseConfig';

export const TenantContext = createContext();

export const TenantProvider = ({ children }) => {
  const [tenants, setTenants] = useState([]);
  const [recentActivities, setRecentActivities] = useState([]);
  const tenantsCollectionRef = collection(db, 'tenants');

  useEffect(() => {
    const fetchTenants = async () => {
      const tenantDocs = await getDocs(tenantsCollectionRef);
      const tenantList = tenantDocs.docs.map(doc => ({ ...doc.data(), id: doc.id }));
      setTenants(tenantList);
    };

    fetchTenants();
  }, []);

  const addTenant = async (tenant) => {
    const tenantRef = await addDoc(tenantsCollectionRef, tenant);
    setTenants([...tenants, { ...tenant, id: tenantRef.id }]);
  };

  const addBill = async (tenantId, bill) => {
    const tenant = tenants.find(t => t.id === tenantId);
    if (tenant) {
      const updatedBills = [...tenant.bills, bill];
      const tenantDocRef = doc(db, 'tenants', tenantId);
      await updateDoc(tenantDocRef, { bills: updatedBills });
      setTenants(tenants.map(t => t.id === tenantId ? { ...t, bills: updatedBills } : t));

      // Add recent activity
      setRecentActivities(prevActivities => {
        const newActivity = {
          message: `A transaction has been added for ${tenant.name}`
        };

        const updatedActivities = [newActivity, ...prevActivities];

        return updatedActivities;
      });
    }
  };

  const deleteBill = async (tenantId, billIndex) => {
    const tenant = tenants.find(t => t.id === tenantId);
    if (tenant) {
      const updatedBills = tenant.bills.filter((_, index) => index !== billIndex);
      const tenantDocRef = doc(db, 'tenants', tenantId);
      await updateDoc(tenantDocRef, { bills: updatedBills });
      setTenants(tenants.map(t => t.id === tenantId ? { ...t, bills: updatedBills } : t));
    }
  };

  const deleteTenant = async (tenantId) => {
    await deleteDoc(doc(db, 'tenants', tenantId));
    setTenants(tenants.filter(t => t.id !== tenantId));
  };

  const clearRecentActivities = () => {
    setRecentActivities([]);
  };

  return (
    <TenantContext.Provider value={{ tenants, addTenant, addBill, deleteBill, deleteTenant, recentActivities, clearRecentActivities }}>
      {children}
    </TenantContext.Provider>
  );
};
