// components/AddBillModal.jsx
import React, { useState, useContext } from 'react';
import { TenantContext } from '../context/TenantContext';
import { Input } from './ui/input';
import { Button } from './ui/button';

const AddBillModal = ({ tenantId, onClose }) => {
  const { addBill } = useContext(TenantContext);
  const [rentBill, setRentBill] = useState('');
  const [gasBill, setGasBill] = useState('');
  const [date, setDate] = useState('');
  const [month, setMonth] = useState('January');

  const handleAddBill = () => {
    if (rentBill && gasBill && date && month) {
      const newBill = {
        rentBill: parseFloat(rentBill),
        gasBill: parseFloat(gasBill),
        date,
        month,
      };
      addBill(tenantId, newBill);
      onClose();
    }
  };

  const isFormComplete = rentBill && gasBill && date && month;

  return (
    <div className="fixed z-50 inset-0 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white dark:bg-[#0C0E0F] dark:text-white p-6 rounded-lg w-11/12">
        <h2 className="text-xl font-bold mb-4">Add Bill</h2>
        <div className="mb-4">
          <label className="block mb-2">Rent Bill</label>
          <Input value={rentBill} onChange={(e) => setRentBill(e.target.value)} type="number" className="p-2" placeholder="Enter rent bill"/>

        </div>
        <div className="mb-4">
          <label className="block mb-2">Gas Bill</label>
          <Input value={gasBill} onChange={(e) => setGasBill(e.target.value)} type="number" className="p-2" placeholder="Enter gas bill"/>

        </div>
        <div className="mb-4">
          <label className="block mb-2">Date</label>
          <Input value={date} onChange={(e) => setDate(e.target.value)} type="date"/>
          
        </div>
        <div className="mb-4">
          <label className="block mb-2">Month</label>
          <Select></Select>

          <select
            value={month}
            onChange={(e) => setMonth(e.target.value)}
            className="w-full p-2 border rounded outline dark:bg-[#0C0E0F]"
          >
            {[
              'January', 'February', 'March', 'April', 'May', 'June',
              'July', 'August', 'September', 'October', 'November', 'December',
            ].map((m) => (
              <option key={m} value={m}>{m}</option>
            ))}
          </select>
        </div>
        <div className="flex justify-end">
          <Button
            onClick={handleAddBill}
            className="px-4 py-2 mr-2"
            disabled={!isFormComplete}
          >
            Add Bill
          </Button>

          <Button onClick={onClose} className="bg-gray-500 px-4 py-2 dark:bg-gray-900 dark:text-white">Cancel</Button>
        </div>
      </div>
    </div>
  );
};

export default AddBillModal;