// components/NotificationModal.jsx
import React from 'react';
import { useNavigate } from 'react-router-dom';

const NotificationModal = ({ tenants, onClose }) => {
  const navigate = useNavigate();
  const today = new Date();
  const currentMonth = today.getMonth() + 1;
  const months = [
    "January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];

  const getOverdueMonths = (bills) => {
    if (bills.length === 0) return months.slice(0, currentMonth);

    const overdueMonths = [];
    const billMonths = bills.map(bill => months.indexOf(bill.month) + 1);

    for (let month = 1; month < currentMonth; month++) {
      if (!billMonths.includes(month)) {
        overdueMonths.push(months[month - 1]);
      }
    }

    return overdueMonths;
  };

  const dueTenants = tenants.filter(tenant => {
    const overdueMonths = getOverdueMonths(tenant.bills);
    return overdueMonths.length > 0;
  });

  const handleTenantClick = (tenantId) => {
    navigate(`/tenantHistory/${tenantId}`);
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-gray-800 bg-opacity-50 flex justify-center items-center">
      <div className="bg-white dark:bg-[#0C0E0F] p-6 rounded-md w-11/12">
        <h2 className="text-xl font-bold mb-4">Notifications</h2>
        <div className="space-y-4">
          {dueTenants.length === 0 ? (
            <p>No notifications</p>
          ) : (
            dueTenants.map((tenant, index) => {
              const overdueMonths = getOverdueMonths(tenant.bills);
              return (
                <div
                  key={index}
                  className="border p-4 rounded-md shadow-md cursor-pointer"
                  onClick={() => handleTenantClick(tenant.id)}
                >
                  <p>{tenant.name} is due on {overdueMonths.join(', ')}</p>
                </div>
              );
            })
          )}
        </div>
        <div className="flex justify-end mt-4">
          <button
            onClick={onClose}
            className="bg-gray-500 text-white px-4 py-2 rounded-md"
          >
            Close
          </button>
        </div>
      </div>
    </div>
  );
};

export default NotificationModal;
