import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { TenantContext } from '../context/TenantContext';
import AddTenantModal from '../components/AddTenantModal';
import ConfirmationModal from '../components/ConfirmationModal';
import { BiUser, BiTrash } from 'react-icons/bi';
import { Button } from '@/components/ui/button';

const Tenant = () => {
  const { tenants, addTenant, deleteTenant } = useContext(TenantContext);
  const [showModal, setShowModal] = useState(false);
  const [showConfirmationModal, setShowConfirmationModal] = useState(false);
  const [tenantToDelete, setTenantToDelete] = useState(null);
  const navigate = useNavigate();

  const handleDelete = () => {
    if (tenantToDelete) {
      deleteTenant(tenantToDelete);
      setShowConfirmationModal(false);
    }
  };

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Tenants</h1>
      <Button onClick={() => setShowModal(true)} className="w-full px-4 py-5 mb-4">Add Tenant</Button>
      <div className="space-y-4">
        {tenants.map((tenant) => (
          <div
            key={tenant.id}
            className="p-4 border rounded-md shadow-md flex items-center justify-between"
          >
            <div onClick={() => navigate(`/tenants/${tenant.id}`)} className="cursor-pointer flex items-center">
              <BiUser size={20} className="mr-3" />
              <p>{tenant.name}</p>
            </div>
            <button
              onClick={() => {
                setTenantToDelete(tenant.id);
                setShowConfirmationModal(true);
              }}
              className="text-red-500 hover:text-red-700"
            >
              <BiTrash size={24} />
            </button>
          </div>
        ))}
      </div>
      {showModal && (
        <AddTenantModal onAdd={addTenant} onClose={() => setShowModal(false)} />
      )}
      {showConfirmationModal && (
        <ConfirmationModal
          isOpen={showConfirmationModal}
          onConfirm={handleDelete}
          onCancel={() => setShowConfirmationModal(false)}
          message="Are you sure you want to delete this tenant?"
        />
      )}
    </div>
  );
};

export default Tenant;
