import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Eye, EyeOff } from 'react-feather';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import AlertPopup from '@/components/AlertPopup';
import { BiLoaderCircle } from "react-icons/bi";

const Login = () => {
  const [pin, setPin] = useState('');
  const [showPin, setShowPin] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleLogin = () => {
    setLoading(true); // Start loading
    setTimeout(() => {
      setLoading(false); // Stop loading after 1200ms
      if (pin === '12345') {
        navigate('/home'); // Redirect to home page
      } else {
        setShowAlert(true); // Show alert popup on incorrect PIN
      }
    }, 1200);
  };

  return (
    <div className="flex items-center justify-center h-screen bg-gray-100">
      <div className="bg-white dark:bg-[#0C0E0F] p-6 rounded-lg shadow-md w-11/12 max-w-md">
        <h2 className="text-2xl font-bold mb-1 text-center">Log In</h2>
        <p className="text-md mb-8 text-gray-600 text-center">Welcome back, you've been missed</p>
        
        <div className="relative space-y-4">
          <Input
            type={showPin ? 'text' : 'password'}
            placeholder="Enter PIN"
            value={pin}
            onChange={(e) => setPin(e.target.value)}
            className="py-5 pr-12"
            disabled={loading} // Disable input during loading
          />
          <button
            type="button"
            onClick={() => setShowPin(!showPin)}
            className="absolute right-3 top-1.5 transform -translate-y-1/2 text-gray-600"
            disabled={loading} // Disable button during loading
          >
            {showPin ? <EyeOff size={20} /> : <Eye size={20} />}
          </button>
        </div>

        <Button onClick={handleLogin} className="w-full py-5 mt-6" disabled={loading}>
          {loading ? (
            <div className="flex justify-center items-center">
              <BiLoaderCircle size={24} className="animate-spin mr-2" />
              Logging in...
            </div>
          ) : (
            'Log in'
          )}
        </Button>
      </div>

      {showAlert && (
        <AlertPopup
          message="Incorrect PIN"
          onClose={() => setShowAlert(false)}
        />
      )}
    </div>
  );
};

export default Login;