import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { TenantContext } from '../context/TenantContext';
import NotificationModal from '../components/NotificationModal';
import Navbar from '../components/Navbar';
import Drawer from '../components/Drawer';
import '../index.css';
import { Button } from '@/components/ui/button';
import { BiMenu, BiSolidBell, BiLogOut, BiLoaderCircle } from "react-icons/bi";

const Home = () => {
  const [showNotification, setShowNotification] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [loading, setLoading] = useState(false);
  const { tenants, recentActivities, clearRecentActivities } = useContext(TenantContext);
  const navigate = useNavigate();

  const today = new Date();
  const currentMonth = today.getMonth() + 1;
  const months = [
    "January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];

  const getOverdueMonths = (bills) => {
    if (bills.length === 0) return months.slice(0, currentMonth);

    const overdueMonths = [];
    const billMonths = bills.map(bill => months.indexOf(bill.month) + 1);

    let firstBillMonth = billMonths[0];
    for (let month = firstBillMonth; month <= currentMonth; month++) {
      if (!billMonths.includes(month)) {
        overdueMonths.push(months[month - 1]);
      }
    }

    return overdueMonths;
  };

  const dueTenants = tenants.filter(tenant => {
    const overdueMonths = getOverdueMonths(tenant.bills);
    return overdueMonths.length > 0;
  });

  const notificationCount = dueTenants.length;

  const handleLogout = () => {
    setLoading(true);
    setTimeout(() => {
      setShowDrawer(false);  // Close the drawer smoothly
      setTimeout(() => {
        navigate('/');  // Redirect to login after drawer is fully closed
      }, 300); // Adjust this to match the drawer's closing animation duration
    }, 1200); // 1200 milliseconds delay before closing the drawer
  };

  return (
    <div className="flex flex-col h-full">
      <div className="flex-grow">
        <header className="flex justify-between items-center mb-6 bg-stone-100">
          <div>
            <BiMenu
              size={40}
              className="ml-4 bg-gray-200 rounded-full p-2 dark:bg-[#0C0E0F]"
              onClick={() => setShowDrawer(true)}
            />
          </div>
          <div className="relative p-4">
            <button
              onClick={() => setShowNotification(true)}
              className="p-2 rounded-full bg-gray-200 flex items-center justify-center dark:bg-[#0C0E0F]"
            >
              <BiSolidBell size={24} />
              {notificationCount > 0 && (
                <div className="absolute top-3 right-3 h-5 w-5 bg-red-500 text-white rounded-full flex items-center justify-center text-xs">
                  {notificationCount}
                </div>
              )}
            </button>
          </div>
        </header>

        {showNotification && (
          <NotificationModal onClose={() => setShowNotification(false)} tenants={tenants} />
        )}

        <section>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-h2 ml-3">Recent Activity</h2>
            <Button onClick={clearRecentActivities} className="mx-5">Clear Recent Activity</Button>
          </div>
          <div className="space-y-4">
            {recentActivities.length === 0 ? (
              <p className="px-4 recent">No recent activity</p>
            ) : (
              recentActivities.map((activity, index) => (
                <div key={index} className="border p-4 rounded-md shadow-md">
                  <p>{activity.message}</p>
                </div>
              ))
            )}
          </div>
        </section>
      </div>
      <Navbar />

      <Drawer isOpen={showDrawer} onClose={() => setShowDrawer(false)}>
        <div
          className="dark:bg-[#0C0E0F] mt-7 p-1.5 bg-white flex cursor-pointer items-center"
          onClick={handleLogout}
        >
          {loading ? (
            <BiLoaderCircle size={20} className="mr-3 mt-0.5 animate-spin" />
          ) : (
            <BiLogOut size={20} className="mr-3 mt-0.5" />
          )}
          {loading ? 'Logging Out...' : 'Log Out'}
        </div>
      </Drawer>
    </div>
  );
};

export default Home;
