// components/Navbar.jsx
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { BiHomeAlt, BiUser, BiCog } from 'react-icons/bi';

const Navbar = () => {
  const location = useLocation();
  const isActive = (path) => location.pathname === path;

  return (
    <nav className="fixed bottom-0 w-full bg-stone-100 text-black flex justify-around py-3">
      <Link to="/home" className="flex flex-col items-center">
        <div
          className={`flex flex-col items-center transition-transform duration-500 ${isActive('/home') ? 'dark:bg-[#fff] dark:text-[#0C0E0F] bg-[#0C0E0F] text-white p-2 rounded-full shadow-lg transform translate-y-[-20px]' : 'text-black'}`}
        >
          <BiHomeAlt size={24} />
          {!isActive('/home') && <span className="text-sm">Home</span>}
        </div>
      </Link>
      <Link to="/tenants" className="flex flex-col items-center">
        <div
          className={`flex flex-col items-center transition-transform duration-500 ${isActive('/tenants') ? 'dark:bg-[#fff] dark:text-[#0C0E0F] bg-[#0C0E0F] text-white p-2 rounded-full shadow-lg transform translate-y-[-20px]' : 'text-black'}`}
        >
          <BiUser size={24} />
          {!isActive('/tenants') && <span className="text-sm">Tenants</span>}
        </div>
      </Link>
      <Link to="/settings" className="flex flex-col items-center">
        <div
          className={`flex flex-col items-center transition-transform duration-500 ${isActive('/settings') ? 'dark:bg-[#fff] dark:text-[#0C0E0F] bg-[#0C0E0F] text-white p-2 rounded-full shadow-lg transform translate-y-[-20px]' : 'text-black'}`}
        >
          <BiCog size={24} />
          {!isActive('/settings') && <span className="text-sm">Settings</span>}
        </div>
      </Link>
    </nav>
  );
};

export default Navbar;
