// src/components/Settings.jsx
import React, { useState, useEffect } from 'react';
import CustomSwitch from "@/components/CustomSwitch";

const Settings = () => {
  // Initialize darkMode from localStorage or default to false
  const [darkMode, setDarkMode] = useState(() => {
    const savedMode = localStorage.getItem('darkMode');
    return savedMode === 'true' ? true : false;
  });

  useEffect(() => {
    // Toggle dark mode class on the document element
    document.documentElement.classList.toggle('dark', darkMode);
    
    // Save the dark mode preference to localStorage
    localStorage.setItem('darkMode', darkMode);
  }, [darkMode]);

  const handleDarkModeToggle = () => {
    setDarkMode(prevMode => !prevMode);
  };

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Settings</h1>
      <div className="flex items-center justify-between mb-4 bg-stone-100 p-5 rounded-lg dark:bg-[#09142c] dark:text-[#fff]">
        <span>Dark Mode</span>
        <CustomSwitch checked={darkMode} onCheckedChange={handleDarkModeToggle} />
      </div>
    </div>
  );
};

export default Settings;
