// pages/TenantHistory.jsx
import React, { useContext, useState } from 'react';
import { useParams } from 'react-router-dom';
import { TenantContext } from '../context/TenantContext';
import AddBillModal from '../components/AddBillModal';
import BillList from '../components/BillList';
import { Button } from '@/components/ui/button';

const TenantHistory = () => {
  const { id } = useParams();
  const { tenants, addBill, deleteBill } = useContext(TenantContext);
  const [showAddBillModal, setShowAddBillModal] = useState(false);
  const tenant = tenants.find(t => t.id === id);

  if (!tenant) return <p>Tenant not found</p>;

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Tenant History</h1>
      <div className="border p-4 rounded-md shadow-md">
        <p><strong>Name:</strong> {tenant.name}</p>
        <p><strong>Contact:</strong> {tenant.contact}</p>
        <p><strong>Floor:</strong> {tenant.floor}</p>
        <p><strong>NID:</strong> {tenant.nid}</p>
      </div>
      <Button onClick={() => setShowAddBillModal(true)} className="my-6 p-5 w-full dark:bg-[#fff]">Add Bill</Button>
      {showAddBillModal && (
        <AddBillModal tenantId={tenant.id} onAdd={addBill} onClose={() => setShowAddBillModal(false)} />
      )}
      <BillList bills={tenant.bills} onDelete={(index) => deleteBill(tenant.id, index)} />
    </div>
  );
};

export default TenantHistory;
