// components/BillList.jsx
import React from 'react';
import {
  Table,
  TableHeader,
  TableBody,
  TableFooter,
  TableRow,
  TableHead,
  TableCell,
} from '../components/ui/table';

const BillList = ({ bills, onDelete }) => {
  return (
    <div className="overflow-x-auto">
      <Table className="min-w-full bg-white border border-gray-200">
        <TableHeader>
          <TableRow>
            <TableHead className="px-4 py-2 border-b">Rent</TableHead>
            <TableHead className="px-4 py-2 border-b">Gas</TableHead>
            <TableHead className="px-4 py-2 border-b">Date</TableHead>
            <TableHead className="px-4 py-2 border-b">Month</TableHead>
            <TableHead className="px-4 py-2 border-b">Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {bills.length === 0 ? (
            <TableRow>
              <TableCell colSpan="5" className="text-center dark:text-[#94A3B8]">No bills found</TableCell>
            </TableRow>
          ) : (
            bills.map((bill, index) => (
              <TableRow key={index}>
                <TableCell className="px-4 py-2 border-b dark:text-black">{bill.rentBill}</TableCell>
                <TableCell className="px-4 py-2 border-b dark:text-black">{bill.gasBill}</TableCell>
                <TableCell className="px-4 py-2 border-b dark:text-black">{bill.date}</TableCell>
                <TableCell className="px-4 py-2 border-b dark:text-black">{bill.month}</TableCell>
                <TableCell className="px-4 py-2 border-b dark:text-black">
                  <button
                    onClick={() => onDelete(index)}
                    className="text-red-500"
                  >
                    Delete
                  </button>
                </TableCell>
              </TableRow>
            ))
          )}
        </TableBody>
      </Table>
    </div>
  );
};

export default BillList;
